ul {
    padding: 0;
}

html {
    box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
    height: 100%;
    line-height: 1.6;
    font-size: 100%;
    font-family: 'Open Sans', sans-serif;
}

body {
    height: 100%;
    padding: 1.6rem .8rem;
}

a {
    text-decoration: none;
    color: #247ba0;
}

a:hover {
    text-decoration: underline;
}

img {
    max-width: 100%;
}

/**
 * Shared bottom margins. Bottom margin = line-height
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
.main-image,
.site-header {
    margin-bottom: 1.6rem;
}

.main-image__img {
  display: none;
  max-width: 17rem;

  @media (min-width: 42em) {
    display: block;
  }
}

.main-image__img + .main-image__img {
  margin-top: 1.6rem;
}

.lead-image {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: .8rem;
  margin-bottom: 1.6rem;

  @media (min-width: 42em) {
    display: none;
  }
}

.site-header {
    text-align: center;
}

.site-title__main,
.site-title__sub {
    display: block;
}

.site-title__main {
    font-size: 2.6rem;
}

.site-title__main:after {
    content: "";
    width: 1.6rem;
    display: block;
    border-top: 2px solid #247ba0;
    margin: 0 auto;
}

.wrapper {
    margin: 0 auto;
    max-width: 58.25rem;
}

.site-footer {
    margin-bottom: 3.2rem;
    text-align: center;
}

.site-footer__title {
  margin-bottom: 1.6rem;
  font-size: 1rem;
}

.cv {
    margin-bottom: 3.2rem;
    padding-top: 1.6rem;
    text-align: center;
}

.btn {
    font-size: .875rem;
    color: #fff;
    display: inline-block;
    padding: .6rem 2.4rem;
    background-color: #247ba0;
    border-radius: 5%;
    transition: background-color 0.5s ease;
}

.btn:hover {
    text-decoration: none;
    background-color: #1b5b76;
}

.footer-list {
    list-style: none;
}

.footer-list li {
    margin-bottom: .8rem;
}

@media (min-width: 42em) { 
    html {
        font-size: 106.25%;
    }

    body {
        padding: 1.6rem;
    }
}

/* @media (min-width: 46em) { */
@media (min-width: 42em) {
    .wrapper {
      display: flex;
      align-items: flex-start;
      margin-top: 3.2rem;
    }

    .main-content {
        flex: 1;
        padding-left: 3.2rem;
    }

    .site-title__main {
        font-size: 2.4rem;
    }
}

@media (min-width: 44em) {
    .site-title__main {
        font-size: 2.8rem;
    }

    .footer-list li {
        display: inline-block;
    }

    .footer-list li:first-child {
        padding-right: .8rem;
    }
}

@media (min-width: 52.5em) { 
    .footer-list {
        font-size: 0;
        text-align: left;
    }

    .footer-list li {
        font-size: 1rem;
        display: inline-block;
        width: 50%;
        padding-left: .8rem;
        margin-bottom: 0;
    }

    .footer-list li:first-child {
        text-align: right;
        padding-left: 0;
        padding-right: .8rem;
    }
}

@media (min-width: 80em) { 
    html {
        font-size: 112.5%;
    }

    .site-title__main {
        font-size: 3.2rem;
    }
}

@media (min-width: 105em) { 
    html {
        font-size: 118.75%;
    }
}


